          <el-select v-model="tmpexecuteplan.usetype" placeholder="" @change="ustypeChanged($event)">
            <el-option label="" value="" />
            <el-option label="" value="" />
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="enviromentname"  required>
          <el-select v-model="tmpexecuteplan.enviromentname" placeholder="" @change="enviromentselectChanged($event)">
