              <el-option :label="envname.enviromentname" :value="envname.enviromentname" required/>
            </div>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="businesstype"  required>
          <el-select v-model="tmpexecuteplan.businesstype" placeholder="">
