              <el-option :label="dicitem.dicitmevalue" :value="dicitem.dicitmevalue" required/>
            </div>
          </el-select>
        </el-form-item>

        <div v-if="PerformanceVisible">
          <el-form-item label="" prop="runmode" required>
            <el-select v-model="tmpexecuteplan.runmode" placeholder="">
              <el-option label="" value="" />
              <el-option label="" value="" />
            </el-select>
          </el-form-item>
