          <el-form-item label=":" prop="deployunitname" required>
            <el-select v-model="searchcase.deployunitname" placeholder="" @change="selectChanged($event)">
              <el-option label="" value />
              <div v-for="(depname, index) in deployunitList" :key="index">
                <el-option :label="depname.deployunitname" :value="depname.deployunitname" required/>
              </div>
            </el-select>
          </el-form-item>
          <el-form-item label="API:">
            <el-select v-model="searchcase.apiname" placeholder="api">
              <el-option label="" value />
              <div v-for="(api, index) in apiList" :key="index">
                <el-option :label="api.apiname" :value="api.apiname"/>
              </div>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="searchcaseBy" :loading="btnLoading"></el-button>
          </el-form-item>
        </span>
        </el-form>
      </div>
